.collection_banner_head {
  display: grid;
  grid-template-columns: 1fr;
  gap: 0;
  @media screen and (min-width: 1024px) {
    grid-template-columns: 61% 39%;
    gap: 0;
  }
}
.banner_head {
  background-color: #F6FAF5;
  .collection_header {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    align-items: center;
    @media screen and (min-width: 1024px) {
      grid-column: 2 / span 1;
      grid-row: 1 / 1;
      align-items: center;
    }
    .text {
      max-width: 520px;
      text-align: center;
      @media screen and (min-width: 1024px) {
        text-align: left;
      }
      @media screen and (min-width: 1200px) {
        padding: 0 24px;
      }
    }
  }
  .collection_image {
    grid-column: 1 / span 1;
    grid-row: 1 / 1;
    @media screen and (min-width: 1024px) {
      grid-column: 1 / span 1;
      grid-row: 1 / 1;
    }
    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }
}

.naked_head-banner {
  text-align: center;
  &.top {
    border-bottom: 1px solid rgba(196, 196, 196, 0.25);
    padding: 40px 0 24px;
  }
  .rte {
    max-width: 520px;
    margin: 0 auto;
  }
}

.collection {
  position: relative;
}

@media screen and (max-width: 749px) {
  .collection .grid__item:only-child {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .collection .title:not(.title--no-heading) {
    margin-top: -1rem;
  }
}

@media screen and (min-width: 750px) {
  .collection .title-wrapper-with-link--no-heading {
    margin-top: calc(6rem + var(--page-width-margin));
  }
}

@media screen and (max-width: 989px) {
  .collection .slider.slider--tablet {
    margin-bottom: 1.5rem;
  }
}

.collection .loading-overlay {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  width: 100%;
  padding: 0 1.5rem;
  opacity: 0.7;
}

@media screen and (min-width: 750px) {
  .collection .loading-overlay {
    padding-left: 5rem;
    padding-right: 5rem;
  }
}

.collection.loading .loading-overlay {
  display: block;
}

.collection--empty .title-wrapper {
  margin-top: 10rem;
  margin-bottom: 15rem;
}


.filtering {
  flex: 0 1 auto;
  max-width: 810px;
  position: relative;
  padding: 16px 0 24px 0;
  fieldset {
    padding: 0;
    .flex {
      gap: 10px;
      flex-wrap: nowrap;
    }
  }
  legend {
    flex: 1 0 100%;
    font-size: 87.5%;
    padding: 8px 0;
  }
  button {
    display: block;
    flex: 0 1 126px;
    max-width: 126px;
    border-color: rgba(0,0,0,.09);
    padding: 0 0 8px 0;
    text-align: center;
    min-width: 60px;
    transition: var(--transition);
    span, img {
      display: block;
    }
    img {
      max-width: 126px;
      height: auto;
      margin: 0 auto;
    }
    span {
      font-weight: 400;
      font-size: 81.25%;
      width: 100%;
      line-height: 1.2;
      padding: 16px 8px 0 8px;
    }
    &.mixitup-control-active {
      border-color: var(--color-red);
    }
    &:hover {
      border-color: rgba(var(--color-red-rgb), 0.3);
    }
  }
  button.reset {
    font-weight: 400;
    padding: 2px;
    font-size: 62.5%;
    position: absolute;
    text-transform: uppercase;
    top: 12px;
    right: 0;
    border:1px solid rgba(0,0,0,.09);
    border-radius: 3px;
    &.mixitup-control-active {
      opacity: 0;
    }
  }
}
.sorting, button.reset {
  flex: 1 1 auto;
}

// Old School Search Results
.template-search__header {
  padding-top: 56px;
}
.template-search__search {
  padding: 16px;
  form {
    max-width: 500px;
    margin: 0 auto;
    button {
      margin-top: 12px;
    }
  }
}
.template-search__results {
  .card__badge {
    position: absolute;
    top: 3px;
    left: 3px;
    .badge {
      border-radius: 0 12px 12px 12px;
    }
  }
  div.square:empty {
    display: block;
    width: 100%;
    padding-top: 100%;
    background-color: var(--color-lightpink);
  }
  .card__text.h5 {
      font-family: var(--font-heading-family);
      margin: 0;
      font-size: 100%;
      text-align: center;
      margin-top: 12px;
    }
}

